import cssString from "./style.js";
const $text = $("#text");
const $style = $("<style>");
//å®žé™…ä½¿ç”¨çš„css,ç”¨äºŽæ’å…¥åˆ°headä¸­
let i = 0;
//ä¸ŽcssStringæ¯”è¾ƒï¼Œç”¨äºŽsubstring(0, i)
let t;
//ç”¨äºŽå‘½åsetTimeout ä»¥ä¾¿è¿›è¡ŒclearTimeout
let k = false;
//ç”¨äºŽåˆ¤æ–­æ˜¯å¦åœ¨æ’­æ”¾ï¼Œè‹¥åœ¨æ’­æ”¾ï¼Œå†ç‚¹å‡»åˆ™æ’­æ”¾æ— æ•ˆ é»˜è®¤false æ’­æ”¾æ—¶ä¸ºtrue
let time = 50;

const player = {
  init: () => {
    $("head").append($style);
    gsap.to("#turbulence", {
      duration: 12,
      repeat: -1,
      ease: "linear",
      attr: {
        baseFrequency: 0.01,
      },
    });
    player.run();
    player.blindEvents();
  },

  hash: {
    ".stop": "pause",
    ".continue": "continue",
    ".slow": "slow",
    ".normal": "normal",
    ".fast": "fast",
  },

  blindEvents: () => {
    for (let key in player["hash"]) {
      if (player.hash.hasOwnProperty(key)) {
        const value = player.hash[key];
        $(`${key}`).on("click", player[value]);
      }
    }
  },

  run: () => {
    $text[0].innerText = cssString.substring(0, i);
    $style[0].innerHTML = cssString.substring(0, i);
    i++;
    //å¾€textDivå’Œheadä¸­å†™css
    k = true;
    $("#textDiv")[0].scrollTop = $("#textDiv")[0].scrollHeight;
    //å¾€ä¸‹æ»šåŠ¨
    i < cssString.length
      ? (t = setTimeout(player.run, time))
      : //cssStringæ²¡è¯»å®Œå°±ç»§ç»­è¯»
        ($text[0].innerHTML = cssString);
    //cssStringè¯»å®Œäº†å°±ä¸€ç›´æ˜¾ç¤ºcssStringï¼Œé˜²æ­¢ç‚¹å‡»æ’­æ”¾å‡ºçŽ°undefined
  },
  //ä½¿ç”¨setTimeoutæ¨¡æ‹ŸsetInterval æ’­æ”¾åŠ¨ç”»

  pause: () => {
    clearTimeout(t);
    k = false;
  },

  continue: () => {
    k ? undefined : player.run();
  },

  slow: () => {
    player.pause();
    time = 100;
    player.run();
  },

  normal: () => {
    player.pause();
    time = 50;
    player.run();
  },

  fast: () => {
    player.pause();
    time = 0;
    player.run();
  },
};
player.init();
